function Section0() {
	let arr = ['GT', 'VITS', 'YTTS', 'ELF_1', 'ELF_20', 'ELF_all']
	let arr_name = ['Ground Truth', 'VITS multi-speaker (Seen)', 'YourTTS', 'Ours (# of audio = 1)', 'Ours (# of audio = 20)', 'Ours (all audio)']
	let brr = [
		"It was not a dangerous exploit for he could always pretend that he had simply looked in to ask a question.",
		"We had an index like a steam-gauge, called an ampere-meter, to indicate the amount of current going out.",
		"We indeed!' cried the Mouse, who was trembling down to the end of his tail.",
		"Civic leaders yesterday praised the good behaviour of revellers across the country.",
		"This would indicate a surge in inflation was unlikely.",
		"Where was the use of imagining?"
	]

	return (
		<table className="w-full table-fixed">
			<tbody>
				<tr>
					<td style={{ minWidth: 200, maxWidth: 200}} ></td>
					{brr.map((sentence) => (
						<td style={{minWidth: 210}}>{sentence}</td>
					))}
				</tr>
				{arr.map((model, i) => (
					<tr key={model}>
						<td>{arr_name[i]}</td>
						{brr.map((sentence, idx) => (
							<td><audio controls preload="none" style={{width: "100%"}}><source src={"demo_samples/Section0/" + model + "_wav_" + idx.toString().padStart(3, '0') + ".wav"} /></audio></td>
						))}
					</tr>
				))}
			</tbody>
		</table>
	);
}

export default Section0;
